import React, { useState, useEffect } from 'react'
import { Button, Modal } from 'antd'
import { Form, Input, Switch } from 'antd'
import { ExclamationCircleOutlined } from '@ant-design/icons'

const initialAgency = {
  loginId: '',
  password: '',
  username: '',
  mobile: '',
  active: 'Y',
}

const AgencyForm = (props) => {
  const { requesting, errors, agency, removeError } = props
  const {
    open,
    handleClose,
    handleExit,
    handleCreate,
    handleUpdate,
    handleDelete,
  } = props
  const [inputs, setInputs] = useState(initialAgency)

  useEffect(() => {
    agency && agency.id && setInputs(agency)
  }, [agency])

  const handleInputChange = (e) => {
    setInputs({ ...inputs, [e.target.name]: e.target.value })
    removeErrorWithName(errors, e.target.name)
  }
  const handleActiveChane = (value) => {
    setInputs({ ...inputs, active: value ? 'Y' : 'N' })
    removeErrorWithName(errors, 'active')
  }
  const removeErrorWithName = (errors, name) => {
    errors && errors[name] && removeError({ name })
  }

  const handleOnCreate = () => handleCreate(inputs)
  const handleOnUpdate = () => handleUpdate(inputs)
  const handleDeleteConfirm = () => {
    Modal.confirm({
      title: ' ?',
      icon: <ExclamationCircleOutlined />,
      content: '   .',
      okText: '',
      cancelText: '',
      onOk() {
        handleDelete(inputs)
      },
    })
  }

  const handleModalExit = () => {
    setInputs(initialAgency)
    handleExit()
  }

  const createFooter = [
    <Button key="cancle" onClick={handleClose} disabled={requesting}>
      
    </Button>,
    <Button
      key="create"
      type="primary"
      onClick={handleOnCreate}
      disabled={requesting}
    >
      
    </Button>,
  ]

  const updateFooter = [
    <Button
      key="delete"
      type="primary"
      danger
      onClick={handleDeleteConfirm}
      disabled={requesting}
    >
      
    </Button>,
    <Button key="cancle" onClick={handleClose} disabled={requesting}>
      
    </Button>,
    <Button
      key="update"
      type="primary"
      onClick={handleOnUpdate}
      disabled={requesting}
    >
      
    </Button>,
  ]

  const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  }

  return (
    <Modal
      title={inputs.id ? ' ' : ' '}
      visible={open}
      afterClose={handleModalExit}
      onCancel={handleClose}
      footer={inputs.id ? updateFooter : createFooter}
      maskClosable={false}
    >
      <Form {...layout} style={{ maxWidth: 360 }}>
        <Form.Item
          label=""
          validateStatus={errors && errors.loginId && 'error'}
          help={errors && errors.loginId && errors.loginId[0]}
        >
          <Input
            name="loginId"
            value={inputs.loginId}
            onChange={handleInputChange}
            disabled={requesting}
            placeholder="  4~20"
          />
        </Form.Item>
        <Form.Item
          label=""
          validateStatus={errors && errors.password && 'error'}
          help={errors && errors.password && errors.password[0]}
        >
          <Input
            type="password"
            name="password"
            value={inputs.password}
            onChange={handleInputChange}
            disabled={requesting}
            autoComplete="new-password"
            placeholder={inputs.id ? ' ' : ' 4~16'}
          />
        </Form.Item>
        <Form.Item
          label=""
          validateStatus={errors && errors.username && 'error'}
          help={errors && errors.username && errors.username[0]}
        >
          <Input
            name="username"
            value={inputs.username}
            onChange={handleInputChange}
            disabled={requesting}
            placeholder="20 "
          />
        </Form.Item>
        <Form.Item
          label=""
          validateStatus={errors && errors.mobile && 'error'}
          help={errors && errors.mobile && errors.mobile[0]}
        >
          <Input
            name="mobile"
            value={inputs.mobile}
            onChange={handleInputChange}
            disabled={requesting}
            placeholder=" "
          />
        </Form.Item>
        <Form.Item
          label=""
          validateStatus={errors && errors.active && 'error'}
          help={errors && errors.active && errors.active[0]}
        >
          <Switch
            name="active"
            checked={inputs.active === 'Y'}
            onChange={handleActiveChane}
            disabled={requesting}
          />
        </Form.Item>
      </Form>
    </Modal>
  )
}

export default AgencyForm
