import React, { useEffect, useState } from 'react'
import { Table, Button, Space } from 'antd'
import ActiveStatus from 'components/Common/ActiveStatus'
import format from 'lib/format'
import { LoginOutlined, EditOutlined } from '@ant-design/icons'
import ScrollableAnchor, { goToAnchor } from 'react-scrollable-anchor'
import _ from 'lodash'

const AgencyList = ({
  loading,
  user,
  data,
  meta,
  handleOpen,
  handleAdd,
  handlePageChange,
  forceLogin,
  handleAgencyCeos,
  openPasswordModal,
  handleOpenCeoModal,
}) => {
  const [agencies, setAgencies] = useState([])

  useEffect(() => {
    setAgencies(data.map((agency) => ({ ...agency, isActive: false })))
  }, [data])

  const displayCeoCount = (agency) => (
    <span>
      {agency.depth - user.depth > 1 ? (
        <span style={{ padding: '4px 15px' }}>{agency.ceoCount}</span>
      ) : (
        <Button
          type="link"
          onClick={() =>
            handleAgencyCeos({
              agency: agency.id,
            })
          }
        >
          {agency.ceoCount}
        </Button>
      )}
    </span>
  )

  const defaultColumns = [
    {
      title: '',
      dataIndex: 'username',
      key: 'username',
    },
    {
      title: '',
      key: 'ceoCount',
      align: 'ceoCount',
      render: (text, agency) => displayCeoCount(agency),
    },
    { title: 'ID', dataIndex: 'loginId', key: 'loginid' },
    {
      title: '',
      dataIndex: 'mobile',
      key: 'mobile',
      render: (mobile) => format.phone(mobile),
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      align: 'center',
      render: (date) => format.date(date),
    },
    {
      title: '',
      dataIndex: 'active',
      key: 'active',
      align: 'center',
      render: (active) => (
        <span style={{ color: active === 'Y' ? 'green' : 'red' }}>
          {active === 'Y' ? '' : ''}
        </span>
      ),
    },
    {
      title: '',
      dataIndex: 'actions',
      key: 'actions',
      align: 'center',
      render: (value, agency, index) => (
        <Space>
          <Button onClick={() => handleOpenCeoModal(agency)}>
             
          </Button>
          <Button danger onClick={() => handleOpen(agency)}>
            
          </Button>
        </Space>
      ),
    },
  ]

  // const forceLoginColumn = {
  //   title: '',
  //   dataIndex: 'forceLogin',
  //   key: 'forceLogin',
  //   align: 'center',
  //   render: (value, agency, index) => (
  //     <Button type="link" onClick={() => forceLogin(agency.id)}>
  //       
  //     </Button>
  //   ),
  // }

  // const columns =
  //   user.role !== 'admin'
  //     ? defaultColumns
  //     : [
  //         ...defaultColumns.slice(0, 2),
  //         forceLoginColumn,
  //         ...defaultColumns.slice(2),
  //       ]

  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        {user.depth < 2 && (
          <>
            <Button
              onClick={() => handleOpen()}
              type="primary"
              style={{ marginBottom: 16 }}
            >
               
            </Button>
          </>
        )}
      </div>
      <Table
        loading={loading}
        className="agenciesTable"
        columns={defaultColumns}
        dataSource={agencies}
        rowKey={(agency) => agency.id}
        pagination={false}
        bordered
        rowClassName={(agency) => (agency.isActive ? 'active-agency' : '')}
      />
    </div>
  )
}

export default AgencyList
