import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, Button, message } from 'antd'
import accountApi from 'modules/core/account/api'

const initialInputs = {
  old_password: '',
  new_password: '',
  new_password_confirm: '',
}

const PasswordModal = ({ open, handleClose }) => {
  const [inputs, setInputs] = useState(initialInputs)
  const [requesting, setRequesting] = useState(false)

  useEffect(() => {
    return () => {
      setInputs(initialInputs)
    }
  }, [])

  const handleExit = () => setInputs(initialInputs)

  const handleChange = (e) => {
    console.log(e.target.name, e.target.value)
    setInputs({ ...inputs, [e.target.name]: e.target.value })
  }

  const handleSubmit = () => {
    if (!inputs.old_password || !inputs.new_password || !inputs.new_password_confirm) {
      message.error('  .')
      return
    }
    if (inputs.new_password !== inputs.new_password_confirm) {
      message.error('     .')
      return
    }

    setRequesting(true)
    accountApi
      .password(inputs)
      .then((response) => {
        message.success(' .')
        setRequesting(false)
        handleClose()
      })
      .catch((error) => {
        message.error(error.message)
        setRequesting(false)
      })
  }

  const footer = [
    <Button key="cancle" onClick={handleClose} disabled={requesting}>
      
    </Button>,
    <Button key="create" type="primary" onClick={handleSubmit} disabled={requesting}>
      
    </Button>,
  ]

  return (
    <Modal width={420} title=" " visible={open} afterClose={handleExit} onCancel={handleClose} footer={footer} maskClosable={false}>
      <Form>
        <Form.Item label=" ">
          <Input.Password name="old_password" value={inputs.old_password} onChange={handleChange} />
        </Form.Item>

        <Form.Item label=" ">
          <Input.Password name="new_password" value={inputs.new_password} onChange={handleChange} />
        </Form.Item>

        <Form.Item label=" ">
          <Input.Password name="new_password_confirm" value={inputs.new_password_confirm} onChange={handleChange} />
        </Form.Item>
      </Form>
    </Modal>
  )
}

export default PasswordModal
