import React, { useState, useEffect } from 'react'
import {
  Button,
  Row,
  Col,
  Input,
  Divider,
  Spin,
  Table,
  Space,
  Image,
  Modal,
  message,
} from 'antd'
import moment from 'moment'
import 'moment/locale/ko'
import api from '../../api'
import format from 'lib/format'
import { FileImageTwoTone, ExclamationCircleOutlined } from '@ant-design/icons'

const apiUrl = process.env.REACT_APP_API_URL

const initialPreview = {
  src: null,
  visible: false,
}

const ReviewList = (props) => {
  const [loading, setLoading] = useState(false)
  const [reviews, setReviews] = useState([])
  const [pagination, setPagenation] = useState(null)
  const [preview, setPreview] = useState(initialPreview)

  useEffect(() => {
    fetch({})
  }, [])

  const fetch = (meta) => {
    setLoading(true)
    api
      .fetch(meta)
      .then((response) => {
        console.log(response)
        setReviews(response.data)
        setPagenation({
          page: response.current_page,
          total: response.total,
          lastPage: response.last_page,
          perPage: response.per_page,
        })
      })
      .catch((error) => {
        console.log(error)
      })
      .finally(() => {
        setLoading(false)
      })
  }

  const handlePageChange = (page) => {
    fetch({ page })
  }

  const handleSend = (review) => {
    console.log(review)
    Modal.confirm({
      title: ' ?',
      icon: <ExclamationCircleOutlined />,
      okText: '',
      cancelText: '',
      onOk: () => {
        api
          .sendGifticon(review)
          .then(() => {
            message.success('.')
          })
          .catch((error) => {
            error.message && message.error(error.message)
          })
          .finally(() => {
            fetch({})
          })
      },
      onCancel: () => {
        console.log('!')
      },
    })
  }

  const handleResend = (review) => {
    console.log(review)
    Modal.confirm({
      title: ' ?',
      icon: <ExclamationCircleOutlined />,
      okText: '',
      cancelText: '',
      onOk: () => {
        api
          .resendGifticon(review)
          .then(() => {
            message.success('.')
          })
          .catch((error) => {
            error.message && message.error(error.message)
          })
          .finally(() => {
            fetch({})
          })
      },
      onCancel: () => {
        console.log('!')
      },
    })
  }

  const handleCancel = (review) => {
    console.log(review)
    Modal.confirm({
      title: ' ?',
      content: (
        <div>
               .
          <br />    .
        </div>
      ),
      icon: <ExclamationCircleOutlined />,
      okText: '',
      cancelText: '',
      onOk: () => {
        api
          .cancelGifticon(review)
          .then(() => {
            message.success('.')
          })
          .catch((error) => {
            error.message && message.error(error.message)
          })
          .finally(() => {
            fetch({})
          })
      },
      onCancel: () => {
        console.log('!')
      },
    })
  }

  const columns = [
    {
      title: '',
      dataIndex: 'agencyName',
      key: 'agencyName',
    },
    {
      title: '',
      dataIndex: 'ceoName',
      key: 'ceoName',
    },
    {
      title: '',
      dataIndex: 'storeName',
      key: 'storeName',
      render: (storeName, review) => {
        return (
          <span style={{ color: review.storeActive === 'N' && 'red' }}>
            <span>{storeName}</span>
            {review.storeActive === 'Y' ? (
              <span style={{ color: 'green' }}>()</span>
            ) : (
              <span style={{ color: 'red' }}>()</span>
            )}
          </span>
        )
      },
    },
    {
      title: '',
      dataIndex: 'mobile',
      key: 'mobile',
      render: (mobile) => format.phone(mobile),
    },
    {
      title: '',
      dataIndex: 'gifticonName',
      key: 'gifticonName',
      render: (gifticonName, review) => (
        <span>
          {gifticonName} ( {review.gifticonPrice} )
        </span>
      ),
    },
    {
      title: '',
      dataIndex: 'ceoCash',
      key: 'ceoCash',
      render: (ceoCash, review) => {
        const cashNum = ceoCash
          ? ceoCash.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
          : 0
        return <span>{cashNum}</span>
      },
    },
    {
      title: '',
      dataIndex: 'image',
      key: 'image',
      align: 'center',
      render: (image) => (
        <span
          style={{ fontSize: 24, cursor: 'pointer' }}
          onClick={() =>
            setPreview({ ...preview, src: apiUrl + image, visible: true })
          }
        >
          <FileImageTwoTone />
        </span>
      ),
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      align: 'center',
      // render: (date) => format.date(date),
    },
    {
      title: '',
      dataIndex: 'done',
      key: 'done',
      align: 'center',
      render: (done) => {
        let color
        if (done === 'Y') color = 'green'
        if (done === 'N') color = 'red'
        if (done === 'C') color = 'gray'
        if (done === 'E') color = 'blue'

        return (
          <span style={{ color: color }}>
            {done === 'Y' && ''}
            {done === 'N' && ''}
            {done === 'C' && ''}
            {done === 'E' && ''}
          </span>
        )
      },
    },
    {
      title: 'Action',
      dataIndex: 'actions',
      key: 'actions',
      align: 'center',
      render: (value, review) => {
        if (review.done === 'Y') {
          return (
            <Space>
              <Button danger onClick={() => handleCancel(review)}>
                
              </Button>
              <Button onClick={() => handleResend(review)}></Button>
            </Space>
          )
        }

        if (review.done === 'C') {
          return <Space></Space>
        }

        return (
          <Button type="primary" onClick={() => handleSend(review)}>
            
          </Button>
        )
      },
    },
  ]

  console.log(preview)

  return (
    <div>
      {reviews && pagination && (
        <Table
          loading={loading}
          className="ceosTable"
          columns={columns}
          dataSource={reviews}
          rowKey={(review) => review.id}
          bordered
          rowClassName={(ceo) => (ceo.active ? 'active-ceo' : '')}
          pagination={{
            current: pagination.current_page,
            pageSize: pagination.per_page,
            total: pagination.total,
            onChange: handlePageChange,
            showSizeChanger: false,
          }}
        />
      )}
      <Image
        src={preview.src}
        style={{ display: 'none' }}
        preview={{
          visible: preview.visible,
          src: preview.src,
          onVisibleChange: (value) => {
            setPreview({
              ...preview,
              visible: value,
              src: value === false ? null : preview.src,
            })
          },
        }}
      />
    </div>
  )
}

export default ReviewList
