import React from 'react'
import { Table, Tag, Space, Button, message, Typography } from 'antd'
import moment from 'moment'

const StoreList = ({
  isAdmin,
  loading,
  downloading,
  stores,
  pagination,
  handleOpen,
  handlePageChange,
  handleGifticonModalOpen,
  handleSearch,
}) => {
  const handleAgencyLink = (agencyId) => handleSearch({ agency: agencyId })

  const columns = [
    {
      title: '',
      dataIndex: 'agencyName',
      key: 'agencyName',
      // render: (agencyName, store) => displayAgencyName(store),
    },
    {
      title: '',
      dataIndex: 'ceoName',
      key: 'ceoName',
      // render: (agencyName, store) => displayAgencyName(store),
    },
    {
      title: '',
      dataIndex: 'cash',
      key: 'cash',
      render: (cash, store) =>
        cash.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ','),
    },
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      render: (name, store) => (
        <span style={{ fontWeight: 'bold' }}>{name}</span>
      ),
    },
    { title: '', dataIndex: 'mobile', key: 'mobile' },
    {
      title: '',
      dataIndex: 'gifticonCount',
      key: 'gifticonCount',
      align: 'center',
      render: (gifticonCount) => gifticonCount,
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      align: 'center',
      render: (createdAt) => (createdAt ? createdAt.substring(0, 10) : ''),
    },
    {
      title: '',
      dataIndex: 'active',
      key: 'active',
      align: 'center',
      render: (active) =>
        active === 'Y' ? (
          <span style={{ color: 'green' }}></span>
        ) : (
          <span style={{ color: 'red' }}></span>
        ),
    },
    {
      title: '',
      dataIndex: 'actions',
      align: 'center',
      key: 'actions',
      render: (text, store) => (
        <Space size="small">
          <Button onClick={() => handleGifticonModalOpen(store)}>
            
          </Button>
          {isAdmin && (
            <Button danger onClick={() => handleOpen(store)}>
              
            </Button>
          )}
        </Space>
      ),
    },
  ]

  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <div></div>
        <div>
          <span style={{ fontWeight: 'bold', marginRight: 15 }}>
            <span style={{ color: 'gray' }}>Total: </span>
            {pagination.total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
          </span>
        </div>
      </div>
      <Table
        bordered
        columns={columns}
        dataSource={stores}
        rowKey={(store) => store.id}
        loading={loading}
        pagination={{
          current: pagination.current_page,
          pageSize: pagination.per_page,
          total: pagination.total,
          onChange: handlePageChange,
          showSizeChanger: false,
        }}
      />
    </div>
  )
}

export default StoreList
